
"use client";

import { useState, useMemo, useEffect } from 'react';
import { EbookCard } from '@/components/ebook-card';
import { getSupabase, type Ebook } from '@/lib/supabase';
import { Input } from '@/components/ui/input';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import { Search } from 'lucide-react';
import { Skeleton } from '@/components/ui/skeleton';

const allCategories = [
  'All',
  'Hacking',
  'Cryptography',
  'Cybersecurity',
  'Malware',
  'Social Engineering',
  'Programming',
  'Penetration Testing',
  'Threat Modeling',
];

const getBookCategory = (title: string): string => {
  title = title.toLowerCase();
  if (title.includes('hacking') || title.includes('hacker')) return 'Hacking';
  if (title.includes('cryptography')) return 'Cryptography';
  if (title.includes('malware')) return 'Malware';
  if (title.includes('social engineering')) return 'Social Engineering';
  if (title.includes('python')) return 'Programming';
  if (title.includes('metasploit') || title.includes('pen-testing'))
    return 'Penetration Testing';
  if (title.includes('threat modeling')) return 'Threat Modeling';
  if (title.includes('cybersecurity') || title.includes('security')) return 'Cybersecurity';
  return 'Cybersecurity';
};

export default function StorePage() {
  const [ebooks, setEbooks] = useState<Ebook[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('All');

  useEffect(() => {
    const fetchEbooks = async () => {
      setLoading(true);
      const supabase = getSupabase();
      const { data, error } = await supabase
        .from('ebooks')
        .select('*');
      
      if (error) {
        console.error('Error fetching ebooks:', error);
      } else {
        setEbooks(data || []);
      }
      setLoading(false);
    };
    fetchEbooks();
  }, []);

  const filteredEbooks = useMemo(() => {
    return ebooks
      .filter((ebook) => {
        const lowerCaseSearch = searchTerm.toLowerCase();
        return (
          ebook.title.toLowerCase().includes(lowerCaseSearch) ||
          ebook.author.toLowerCase().includes(lowerCaseSearch)
        );
      })
      .filter((ebook) => {
        if (selectedCategory === 'All') {
          return true;
        }
        return getBookCategory(ebook.title) === selectedCategory;
      });
  }, [ebooks, searchTerm, selectedCategory]);

  return (
    <div className="container mx-auto px-4 py-8 md:py-12">
      <div className="text-center">
        <h1 className="font-headline text-3xl md:text-4xl font-bold mb-2">
          The Store
        </h1>
        <p className="text-muted-foreground mb-8 max-w-2xl mx-auto">
          Browse our curated collection of ebooks on cybersecurity, hacking, and internet technology.
        </p>
      </div>

      <div className="mb-8 flex flex-col sm:flex-row gap-4">
        <div className="relative flex-grow">
          <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-5 w-5 text-muted-foreground" />
          <Input
            type="text"
            placeholder="Search by title or author..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="pl-10 w-full"
          />
        </div>
        <div className="w-full sm:w-[200px]">
          <Select
            value={selectedCategory}
            onValueChange={setSelectedCategory}
          >
            <SelectTrigger>
              <SelectValue placeholder="Filter by category" />
            </SelectTrigger>
            <SelectContent>
              {allCategories.map((category) => (
                <SelectItem key={category} value={category}>
                  {category}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
      </div>
      
      {loading ? (
         <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-8">
            {Array.from({ length: 8 }).map((_, i) => (
              <div key={i} className="space-y-2">
                <Skeleton className="aspect-[2/3] w-full" />
                <Skeleton className="h-5 w-5/6" />
                <Skeleton className="h-4 w-3/4" />
                <Skeleton className="h-8 w-1/2" />
              </div>
            ))}
        </div>
      ) : filteredEbooks.length > 0 ? (
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-8">
          {filteredEbooks.map((ebook) => (
            <EbookCard key={ebook.id} ebook={ebook} />
          ))}
        </div>
      ) : (
        <div className="text-center py-16 border-2 border-dashed rounded-lg">
          <h2 className="text-2xl font-semibold mb-2">No Books Found</h2>
          <p className="text-muted-foreground">
            Your search for "{searchTerm}" in {selectedCategory} did not return any results.
          </p>
        </div>
      )}
    </div>
  );
}
